.Login {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f7f7f7;
}

.Login .Login-content {
    width: 50%;
    padding: 30px;
    color: #000;
    font-family: Poppins;
    background-color: #ffff;
}

.Login .Login-content .form-control:focus {
    box-shadow: none;
    border: 1px solid grey;
}

.Login .Login-content .btn {
    background-color: #000;
    border-radius: 0;
    color: #fff;
    width: 30%;
    display: block;
    margin: 0 auto;
}

.Login .Login-content .btn:focus {
    box-shadow: none;
}